/* 인트로 */
.body {
  overflow: hidden;
  background-color: #121212;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.body.off{
  animation: loading ease-out 3s;
}
@keyframes loading {
  0%{opacity: 1; transform: scale(1);}
  99%{transform: scale(10);}
  100%{transform: scale(10); opacity: 0; display: none;}
}

.loading{
  display: block;
  width: 200px; height: 100px;
  position: absolute; top: 70%; left: 50%;
  transform: translateX(-50%);
  font-size: 40px;
  color: #fff;
  letter-spacing: -1px;
  font-family: 'DungGeunMo';
  background: none;
}
 .rainbow, .nyan{
  width: 5px;
  height: 5px;
}

:root {
  --window-width: 0;
}

.rainbow {
  z-index: 100;
  margin-top: 100px;
  margin-right: -15px;
  margin-left: -150px;
  width: 160px;
  height: 95px;
  background: linear-gradient(to bottom, #ff2121 0%, #ff2121 16.6666666667%, #ffaf1e 16.6666666667%, #ffaf1e 33.3333333333%, #ffff10 33.3333333333%, #ffff10 50%, #4dff17 50%, #4dff17 66.6666666667%, #21b5ff 66.6666666667%, #21b5ff 83.3333333333%, #8454ff 83.3333333333%, #8454ff 100%), linear-gradient(to bottom, #ff2121 0%, #ff2121 16.6666666667%, #ffaf1e 16.6666666667%, #ffaf1e 33.3333333333%, #ffff10 33.3333333333%, #ffff10 50%, #4dff17 50%, #4dff17 66.6666666667%, #21b5ff 66.6666666667%, #21b5ff 83.3333333333%, #8454ff 83.3333333333%, #8454ff 100%), linear-gradient(to bottom, #ff2121 0%, #ff2121 16.6666666667%, #ffaf1e 16.6666666667%, #ffaf1e 33.3333333333%, #ffff10 33.3333333333%, #ffff10 50%, #4dff17 50%, #4dff17 66.6666666667%, #21b5ff 66.6666666667%, #21b5ff 83.3333333333%, #8454ff 83.3333333333%, #8454ff 100%), linear-gradient(to bottom, #ff2121 0%, #ff2121 16.6666666667%, #ffaf1e 16.6666666667%, #ffaf1e 33.3333333333%, #ffff10 33.3333333333%, #ffff10 50%, #4dff17 50%, #4dff17 66.6666666667%, #21b5ff 66.6666666667%, #21b5ff 83.3333333333%, #8454ff 83.3333333333%, #8454ff 100%);
  background-repeat: no-repeat;
  background-size: 40px 90px;
  --p1: 5px;
  --p2: 0;
  background-position: 0 var(--p1), 40px var(--p2), 80px var(--p1), 120px var(--p2);
  animation: rainbow infinite 0.425s step-end;
}
@keyframes rainbow {
  50% {
    --p1: 0;
    --p2: 5px;
  }
}

